const { createClient } = require('@supabase/supabase-js');
const path = require('path');
require('dotenv').config({ path: path.join(__dirname, '../../.env') });

// Configura√ß√£o do Supabase
const supabaseUrl = process.env.SUPABASE_URL;
const supabaseKey = process.env.SUPABASE_SERVICE_ROLE_KEY;

if (!supabaseUrl || !supabaseKey) {
    console.error('‚ùå Vari√°veis de ambiente SUPABASE_URL e SUPABASE_SERVICE_ROLE_KEY s√£o obrigat√≥rias');
    process.exit(1);
}

const supabase = createClient(supabaseUrl, supabaseKey);

/**
 * Limpa todas as tabelas de dados importados
 */
async function cleanImportedData() {
    console.log('üßπ Iniciando limpeza de dados importados...');
    
    try {
        // 1. Limpar pagamentos primeiro (devido √†s foreign keys)
        console.log('üóëÔ∏è  Limpando tabela payments...');
        const { error: paymentsError } = await supabase
            .from('payments')
            .delete()
            .neq('id', '00000000-0000-0000-0000-000000000000'); // Delete all
        
        if (paymentsError) {
            console.error('‚ùå Erro ao limpar payments:', paymentsError);
        } else {
            console.log('‚úÖ Tabela payments limpa');
        }

        // 2. Limpar contratos
        console.log('üóëÔ∏è  Limpando tabela contracts...');
        const { error: contractsError } = await supabase
            .from('contracts')
            .delete()
            .neq('id', '00000000-0000-0000-0000-000000000000'); // Delete all
        
        if (contractsError) {
            console.error('‚ùå Erro ao limpar contracts:', contractsError);
        } else {
            console.log('‚úÖ Tabela contracts limpa');
        }

        // 3. Limpar clientes
        console.log('üóëÔ∏è  Limpando tabela clients...');
        const { error: clientsError } = await supabase
            .from('clients')
            .delete()
            .neq('id', '00000000-0000-0000-0000-000000000000'); // Delete all
        
        if (clientsError) {
            console.error('‚ùå Erro ao limpar clients:', clientsError);
        } else {
            console.log('‚úÖ Tabela clients limpa');
        }

        console.log('üéâ Limpeza conclu√≠da com sucesso!');
        
        // Verificar se as tabelas est√£o vazias
        await verifyCleanup();
        
    } catch (error) {
        console.error('‚ùå Erro durante a limpeza:', error);
        throw error;
    }
}

/**
 * Verifica se a limpeza foi bem-sucedida
 */
async function verifyCleanup() {
    console.log('\nüîç Verificando limpeza...');
    
    try {
        // Contar registros em cada tabela
        const { data: clientsCount, error: clientsError } = await supabase
            .from('clients')
            .select('id', { count: 'exact', head: true });
        
        const { data: contractsCount, error: contractsError } = await supabase
            .from('contracts')
            .select('id', { count: 'exact', head: true });
        
        const { data: paymentsCount, error: paymentsError } = await supabase
            .from('payments')
            .select('id', { count: 'exact', head: true });

        if (clientsError || contractsError || paymentsError) {
            console.error('‚ùå Erro ao verificar limpeza');
            return;
        }

        console.log(`üìä Registros restantes:`);
        console.log(`   üë• Clientes: ${clientsCount?.length || 0}`);
        console.log(`   üìÑ Contratos: ${contractsCount?.length || 0}`);
        console.log(`   üí∞ Pagamentos: ${paymentsCount?.length || 0}`);
        
        const totalRecords = (clientsCount?.length || 0) + (contractsCount?.length || 0) + (paymentsCount?.length || 0);
        
        if (totalRecords === 0) {
            console.log('‚úÖ Todas as tabelas est√£o vazias - pronto para nova importa√ß√£o!');
        } else {
            console.log('‚ö†Ô∏è  Ainda existem registros nas tabelas');
        }
        
    } catch (error) {
        console.error('‚ùå Erro ao verificar limpeza:', error);
    }
}

/**
 * Fun√ß√£o principal
 */
async function main() {
    try {
        console.log('üöÄ Iniciando limpeza para nova importa√ß√£o...\n');
        
        // Confirmar se o usu√°rio quer continuar
        console.log('‚ö†Ô∏è  ATEN√á√ÉO: Esta opera√ß√£o ir√° DELETAR TODOS os dados das tabelas:');
        console.log('   - clients');
        console.log('   - contracts'); 
        console.log('   - payments');
        console.log('\n   Esta a√ß√£o √© IRREVERS√çVEL!\n');
        
        // Em ambiente de produ√ß√£o, voc√™ pode querer adicionar uma confirma√ß√£o interativa
        // Por agora, vamos prosseguir automaticamente
        
        await cleanImportedData();
        
        console.log('\nüéØ Sistema pronto para nova importa√ß√£o!');
        
    } catch (error) {
        console.error('üí• Erro fatal:', error);
        process.exit(1);
    }
}

// Executar se chamado diretamente
if (require.main === module) {
    main();
}

module.exports = { cleanImportedData, verifyCleanup, main };